<template>
  <div :class="themeMode.mode" v-cloak id="app">
    <div class="container">
      <HeaderTop @mode='OnNew' @login='onLogin' />
      <WeatherInfo :themeMode='themeMode' :arrayCity='arrayCity' />
    </div>
  </div>
</template>

<script>
import HeaderTop from "./components/HeaderTop/HeaderTop.vue";
import WeatherInfo from "./components/WeatherInfo/WeatherInfo.vue";

export default {
  props: ['item'],
  components: { HeaderTop, WeatherInfo},
  name: "App",
  data() {
    return {
      arrayCity: [],
      themeMode: '',
    }
  },
  methods: {
    onLogin(data) {
      this.arrayCity = data
    },
    OnNew(item) {
      this.themeMode = item.mode
    }
  },

};
</script>

<style lang="sass" scoped>
[v-cloak]
  display: none

#app 
  width: 100%
  height: 100vh
  background: rgba(0, 0, 0, 0.9)
  transition: 0.3s ease all

#app.dark 
  background: #F4F1F1
</style>
