<template>
  <div class="header">
    <div class="logo">
      <a class="logo-img__link" href="#"
        ><img class="logo__img" src="../../assets/logo-wether.svg" alt="logo"
      /></a>
      <a :class="themeMode.mode" class="logo__desc" href="#">VUE WEATHER</a>
    </div>
    <div class="header__select">
      <input
        :class="themeMode.mode"
        class="header__input"
        placeholder="Look for the city"
        v-model="cityName"
        @change="getCityName"
        type="search"
      />
    </div>
    <div class="header__load" v-if="loading"><img src="../../assets/load.gif" alt=""></div>
    <div data-close="1" @click="closeModalWrapper" :class="{ active: isActive }" class="header__modal">
      <div :class="{ active: isActive }" class="header__modal-inner">
        <p :class="{ active: isActive }" class="header__modal-desc">There is no such city</p>
        <a @click="closeModal" :class="{ active: isActive }" class="header__close" href="#">&times;</a>
      </div>
    </div>  
    <HeaderTheme @theme='onTheme' />
  </div>
</template>

<script>
import HeaderTheme from './HeaderTheme/HeaderTheme.vue';
export default {
  components: { HeaderTheme },
  data() {
    return {
      cityName: "Moscow",
      temper: "",
      nowDay: new Date().toLocaleDateString(),
      nowPressure: "",
      humidity: '',
      windSpeed: '',
      imgWeather: '',
      loading: true,
      isActive: false,
      themeMode: '',
    };
  },
  mounted() {
    if (localStorage.cityName) {
      this.cityName = localStorage.cityName;
      this.getCityName();
    }
    if (this.cityName == 'Moscow') {
      this.getCityName();
    }
  },
  methods: {
    onTheme(item) {
      this.themeMode = item;
      this.$emit('mode', {
        mode: this.themeMode
      })
    },
    getCityName() {
      if (this.cityName.length != 0 ) {
        this.axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${this.cityName}&appid=243a30286c6750a33c71fd9dcf00f259`
        )
        .then((response) => {
          this.temper = Math.round(response.data.main.temp - 273);
          this.nowPressure = response.data.main.pressure;
          this.cityName = response.data.name;
          this.humidity = `${response.data.main.humidity}%`;
          this.windSpeed = response.data.wind.speed;
          this.imgWeather = response.data.weather[0].icon;
          localStorage.cityName = this.cityName;
            this.$emit('login', {
              cityName: this.cityName,
              temper: this.temper,
              nowDay: this.nowDay,
              nowPressure: this.nowPressure,
              humidity: this.humidity,
              windSpeed: this.windSpeed,
              imgWeather: this.imgWeather,
            })
        })
        .catch(error => {
          console.log(error)
          this.isActive = true;
        })
        .finally(() => {
          this.loading = false;
        })
      }
    },
    closeModalWrapper(e) {
      if (e.target.dataset.close == '1') {
        this.isActive = false;
      }
    },
    closeModal(e) {
      e.preventDefault();
      this.isActive = false;
    },
  },

};
</script>

<style lang="sass" scoped>
.header
  display: flex
  justify-content: space-between
  padding-top: 30px
  align-items: center

  li
    color: white

  .logo
    display: flex
    align-items: center
    gap: 20px

  .logo__img
    height: 50px
    width: 50px

  .logo__desc
    color: white
    transition: 0.3s ease all

  .dark.logo__desc
    color: black

  .header__input
    border: none
    outline: none
    padding: 10px
    border-raduis: 10px
    border-radius: 10px
    transition: ease all 0.3s

  .dark.header__input
    border: 1px solid black
    background: #F4F1F1

  .nows
    color: white

  &__load
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)

  &__modal
    opacity: 0
    transition: 0.4s ease all
    position: fixed
    left: 0
    top: 0
    width: 100%
    height: 100%
    pointer-events: none

  &__close
    transition: 0.4s ease all

  &__modal-inner
    display: none
    
  &__modal.active
    position: fixed
    width: 100%
    height: 100%
    background: rgb(0, 0, 0, 0.9)
    opacity: 1
    transition: 0.4s ease all
    pointer-events: all

  &__modal-inner.active
    background-color: #D8D8D8
    position: fixed
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    z-index: 2
    width: 40%
    height: 40%
    border-radius: 10px
    border: 2px solid red
    display: block

  &__modal-desc.active
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)

  &__close.active
    position: absolute
    top: 20px
    right: 20px 
    font-size: 25px
    
@media screen and (max-width: 650px) 
  .logo__desc
    display: none

</style>
